@charset "utf-8";

.l_form *{margin: 0; padding: 0; font-size: 12px;}
.l_form ul, ol, li{list-style: none;}
.l_form a{text-decoration: none; color: #000;}

.l_form{
    width: 100%;
    margin: 0 auto;
    padding: 150px 0 200px 0;
}
.l_form h2{
    text-align: center;
    font-size: 18px;
    margin-bottom: 25px;
}

.l_form ul{
    display: flex;
    margin: 0 auto 30px auto;
    width: 180px;
    padding-left: 200px;
}
.l_form ul > li:first-child::after{content: "|"; margin: 0 5px;}

.l_form input, button{display: block;}
.l_form input{
    font-size: 14px;
    padding: 16px 10px;
    width: 300px;
    margin: 12px auto;
    border-radius: 3px;
    border: 1px solid #ccc;
    outline: none;
}
.l_form input:focus{border: 1px solid #333;}

.btn1, .btn2{
    font-size: 16px;
    width: 325px;
    padding: 14px 10px;
    margin: 12px auto;
    border-radius: 3px;
    cursor: pointer;
}

.btn1{
    background-color: #5f0080;
    color: #fff;
    border: 1px solid #5f0080;
}
.btn2{
    background-color: #fff;
    color: #5f0080;
    border: 1px solid #5f0080;
}

.l_form p{
    text-align: center;
    font-size: 14px;
    padding: 5px;
}