@charset "utf-8";

.product{
    width: 1050px;
    margin: 0 auto;
    padding: 50px 0;
}
.product h2{
    font-size: 28px;
    text-align: center;
    margin-bottom: 50px;
}
.product ul li{cursor: pointer;}
.product ul{
    display: flex;
    justify-content: center;
    padding-bottom: 30px;
}
.product ul li{
    margin-right: 18px;
    position: relative;
}
.product ul li:last-child{margin-right: 0;}
.product .pro02 li:first-child i.fa-cart-shopping{display: none;}
.product ul li .img_box{
    width: 249px; height: 320px;
    overflow: hidden;
}
.product ul li .img_box img{
    width: 100%;
    transition: .5s;
}
.product ul li .img_box:hover img{transform: scale(1.03);}
.product ul li .txt_box{
    padding: 14px 10px 0 0;
}
.product ul li .txt_box .title{
    font-size: 15px;
    margin: 0;
    line-height: 23px;
    margin-bottom: 6px;
}
.product ul li .txt_box .price, .product ul li .txt_box span{
    font-size: 16px;
    font-weight: bold;
    display: inline-block;
    line-height: 26px;
}
.product ul li .txt_box span{color: #fa622f; margin-right: 7px;}
.product ul li .txt_box s{
    font-size: 14px;
    color: #b5b5b5;
    display: block;
}
.product ul li i.fa-cart-shopping{
    padding: 13px 12px;
    background: rgba(62, 23, 73, 0.5);
    color: #fff;
    font-size: 18px;
    border-radius: 50%;
    position: absolute;
    bottom: 107px; right: 17px;
}
.product .pro02 li i.fa-cart-shopping{bottom: 122px;}