@charset "utf-8";

*{margin: 0px; padding: 0px;}
ul,ol,li{list-style: none;}
body{
    font-family: "맑은 고딕", arial, 'snas-serif';
    font-size: 12px;
    color: #333;
}
a{text-decoration: none; color: #333;}

header{
    width: 100%;
    box-shadow: 0px 0px 3px #333;
}
.header_inner{
    width: 1050px; height: 156px;
    margin: 0 auto;
    position: relative;
}

/* lnb */
.lnb ul{
    display: flex;
    justify-content: right;
    padding: 10px 0;
    width: 1050px;
}
.lnb ul li{cursor: pointer;}
.lnb ul li::before{
    content: "";
    display: inline-block;
    width: 1px; height: 13px;
    background: #d9d9d9;
    margin: 0 10px;
}
.lnb ul li:first-child::before{display: none;}
.lnb ul li:first-child{color: #5f0080;}
.lnb ul li:last-child i{margin-left: 5px;}

/* header top */
.header_inner .top{
    position: relative;
    display: flex;
    width: 1050px;
}

.title{display: flex; margin-right: 50px;}
.title h1{padding: 4px 0;}
.title ul{display: flex; margin-left: 20px;}
.title ul li{
    font-size: 17px;
    line-height: 56px;
    font-weight: bold;
    color: #b5b5b5;
    cursor: pointer;
}
.title ul li:first-child, .title ul li:last-child:hover{color: #5f0080;}
.title ul li:first-child::after{
    content: "";
    width: 1px; height: 14px;
    display: inline-block;
    background: #e2e2e2;
    margin: 0 10px
}

.sh_box{
    width: 400px;
    position: relative;
}
.sh_box input{
    border: none;
    width: 400px;
    border: 1px solid #5f0080;
    border-radius: 7px;
    padding: 15px 0 15px 15px;
    font-size: 16px;
    outline: none;
}
.sh_box button{
    position: absolute;
    top: 15px; right: 15px;
    background: none;
    border: none;
    display: block;
    cursor: pointer;
}
.sh_box button i{
    color: #5f0080;
    font-size: 20px;
}

.icons{
    position: absolute;
    right: 0;
    display: flex;
    justify-content: space-between;
    width: 140px;
    padding: 15px 0;
}
.icons li i{
    font-size: 26px;
    cursor: pointer;
}
.icons li i:hover{color: #5f0080;}

/* gnb */
.gnb{position: absolute; bottom:0px;}
.gnb ul{
    display: flex;
}
.gnb ul li:first-child{
    margin-right: 140px;
    padding: 18px 0 !important;
}
.gnb ul li:first-child i{margin-right: 10px;}
.gnb ul li:first-child span:hover{text-decoration: none;}
.gnb ul li{
    font-size: 16px;
    padding: 18px 44px;
    cursor: pointer;
    font-weight: bold;
}
.gnb ul li span{display: block;}
.gnb ul li span:hover{
    color: #5f0080;
    text-decoration: underline;
}

/* footer */
footer{
    width: 100%;
    padding-top: 40px;
    border-top: 1px solid rgb(228, 228, 228);
}
.f_inner{
    width: 1050px;
    margin: 0 auto;
    display: flex;
    position: relative;
}

footer #top_btn{
    position: fixed;
    bottom: 20px; right: 30px;
    z-index: 1000;
    cursor: pointer;
    padding: 14px 17.6px;
    border-radius: 50%;
    border: 1px solid #ccc;
}
footer #top_btn:hover{
    background: rgb(241, 241, 241);
}
#top_btn i{font-size: 26px;}

.f_inner .left h2{font-size: 20px; margin-bottom: 20px;}
.f_inner .left strong{font-size: 28px; margin-right: 10px;}
.f_inner .left span{font-size: 16px;}
.f_inner .right{
    position: absolute;
    right: 0;
    padding-bottom: 30px;
}
.f_inner .right ul{
    display: flex;
    margin-bottom: 30px;
}
.f_inner .right ul li{
    font-size: 14px;
    margin: 0 5px;
    cursor: pointer;
}
.f_inner .right ul li:nth-child(5){font-weight: bold;}
.f_inner .right .address{
    color:#999999;
    line-height: 20px;
}
.f_inner .right .stick{
    width: 1px; height: 10px;
    display: inline-block;
    background: #999999;
    margin: 0 7px;
}
.f_inner .right span{
    color: #5f0080;
    font-weight: bold;
    cursor: pointer;
}