@charset "utf-8";

/* main */
main{margin-bottom: 30px;}

.slide{
    width: 1903px; height: 370px;
    margin: 0 auto 40px auto;
    position: relative;
    overflow: hidden;
}
.slide ul{
    display: flex;
    position: absolute;
    top: 0px; left: 0px;
    width: 9515px;
    transition: .3s;
}
.slide ul li{width: 100%;}
.slide ul li img{width: 100%;}
.slide i.fas{
    position: absolute;
    top: 48%;
    padding: 15px 17px;
    background: rgba(51, 51, 51, .3);
    color: #fff;
    border-radius: 50%;
    font-size: 20px;
    opacity: 0;
    transition: .5s;
}
.slide:hover i.fas{opacity: 1;}
.slide i.fa-chevron-left{left: 300px;}
.slide i.fa-chevron-right{right: 300px;}

.banner h2, .slide h2{display: none;}
.banner{height: 140px;}
.pd, .banner{
    width: 1050px;
    margin: 0 auto;
    padding: 50px 0;
}
.pd ul li, .banner, .pd02 h2{cursor: pointer;}

.pd h2{
    font-size: 28px;
    text-align: center;
    margin-bottom: 27px;
}
.pd .fa-angle-right{margin-left: 10px;}
.pd ul{
    display: flex;
    justify-content: center;
}
.pd ul li{
    margin-right: 18px;
    position: relative;
}
.pd ul li:last-child{margin-right: 0;}
.pd02 ul li:first-child i.fa-cart-shopping{display: none;}
.pd ul li .img_box{
    width: 249px; height: 320px;
    overflow: hidden;
}
.pd ul li .img_box img{
    width: 100%;
    transition: .5s;
}
.pd ul li .img_box:hover img{transform: scale(1.03);}
.pd ul li .txt_box{
    padding: 14px 10px 0 0;
}
.pd ul li .txt_box .title{
    font-size: 15px;
    margin: 0;
    line-height: 23px;
    margin-bottom: 6px;
}
.pd ul li .txt_box .price, .pd ul li .txt_box span{
    font-size: 16px;
    font-weight: bold;
    display: inline-block;
    line-height: 26px;
}
.pd ul li .txt_box span{color: #fa622f; margin-right: 7px;}
.pd ul li .txt_box s{
    font-size: 14px;
    color: #b5b5b5;
    display: block;
}
.pd ul li i.fa-cart-shopping{
    padding: 13px 12px;
    background: rgba(62, 23, 73, 0.5);
    color: #fff;
    font-size: 18px;
    border-radius: 50%;
    position: absolute;
    bottom: 107px; right: 17px;
}
.pd02 ul li i.fa-cart-shopping{bottom: 122px;}

.banner img{width: 100%;}

#page{
    color: #fff;
    background: rgba(51, 51, 51, .3);
    font-size: 15px;
    position: absolute;
    bottom: 22px; right: 410px;
    z-index: 100;
    padding: 4px 10px;
    border-radius: 50px;
}